.Toolbar {
  position: fixed;
  top: 0px;
  left: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  width: 100%;
  z-index: 999;
  background: hsl(200, 80%, 85%);
}

.Toolbar-item {
  cursor: pointer;
  border: 1px solid black;
  padding: 0px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 43px;
  position: relative;
}

.Toolbar-text {
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.Toolbar-logo {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 40px;
}


